import helloComp from "../../components/helloComp.vue"
import trailerStars from "../../components/trailerStars"

export default {
	data() {
		return {
			animationData: {},
			animationDataArr: [
				{}, {}, {}, {}
			]
		}
	},
	onUnload() {
		//页面卸载的时候  清除动画数据
		this.animationData = {}
	},
	onLoad() {
		

		//在页面创建的时候，创建一个临时动画
		this.animation = uni.createAnimation()

		
		//获取后端数据
		uni.request({
		    url: 'https://www.imovietrailer.com/superhero/index/carousel/list',
			method: "POST",
		    success: (res) => {
		        console.log(res.data);
		        this.text = 'request success';
		    }
		});
	},
	methods: {
		//实现点赞动画效果
		praiseMe(e) {
			

			
			//构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-65).opacity(1).step({
				duration:400
				});
			this.animationData = this.animation;
			this.animationDataArr[e] = this.animationData.export();
			
			//还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation
				this.animationDataArr[e] = this.animationData.export();
			}.bind(this), 500);
			

		}
	},
	components: {
		helloComp,
		trailerStars
	}
}
